import React from 'react';
import { MyContext } from "../Context/Context"

export const BookSlot = (props) => {

    const [text, setText] = React.useState('');
    const [email, setEmail] = React.useState('');

    const handleSubmit = async (obj) => {
        console.log(obj, text, email)
        let response = await fetch('https://damp-caverns-01669.herokuapp.com/rooms/' + obj.room.room + '/' + obj.date + '/' + obj.slot, {
            method: 'POST',
            headers: {
                'Content-Type': 'application/json'
            },
            body: JSON.stringify({
                customerName: text.trim(),
                email: email.trim(),
                roomName: obj.room.room,
                date: obj.room.dates[+obj.date].date,
                slot: props.slot,
            })
        }).then((data) => data.json());
        alert(response.message)
        if (response.code !== 2) {
            window.location.reload()
        }
    }

    return (
        <MyContext.Consumer>
            {(value) => {
                return (
                    < div className="card mt-3" >
                        <div className="card-body">
                            <div className="card-title">
                                <p className="text-right cur" onClick={props.hideForm}>X</p>
                                <p className="h4 text-center">{props.slot.slotName + 'Slot'}</p>
                            </div>
                            <hr />
                            <div className="card-text">
                                <form action="">
                                    <div className="row">
                                        <div className="col-sm-12">
                                            <label htmlFor="cName">Your Name</label>
                                            <input
                                                type="text"
                                                id='cName'
                                                className="form-control"
                                                value={text}
                                                onChange={(e) => setText(e.target.value)}
                                                required />
                                        </div>
                                    </div>
                                    <div className="row mt-3">
                                        <div className="col-sm-12">
                                            <label htmlFor="email">Email Id</label>
                                            <input
                                                type="email"
                                                id='email'
                                                className="form-control"
                                                required
                                                value={email}
                                                onChange={(e) => setEmail(e.target.value)} />
                                        </div>
                                    </div>
                                    <div className="row mt-3">
                                        <div className="col-sm-12">
                                            <button className="btn btn-primary"
                                                type='submit'
                                                onClick={(e) => {
                                                    e.preventDefault();
                                                    handleSubmit(value[0])
                                                }}
                                            >Submit</button>
                                        </div>
                                    </div>
                                </form>
                            </div>
                        </div>
                    </div>
                )
            }}
        </MyContext.Consumer >
    )
}