import React from 'react'

export const RoomData = (props) => {

    return (
        <table className="table table-striped">
            <thead className="thead-dark">
                <tr>
                    <th className="text-left">Customer Name</th>
                    <th className="text-left">Room Name</th>
                    <th className="text-left">Booked Date</th>
                    <th className="text-left">Slot</th>
                    <th className="text-left">Start-time</th>
                    <th className="text-left">End-time</th>
                </tr>
            </thead>
            <tbody>
                {props.room.customers.map((i, index) => {
                    return (
                        <tr key={index}>
                            <td className="text-left">{i.customerName}</td>
                            <td className="text-left">{'Room ' + i.roomName}</td>
                            <td className="text-left">{i.date}</td>
                            <td className="text-left">{i.slot.slotName}</td>
                            <td className="text-left">{i.slot.startTime}</td>
                            <td className="text-left">{i.slot.endTime}</td>
                        </tr>


                    )
                })}
            </tbody>
        </table>
    )
}