import React from 'react'
import { MyContext } from '../Context/Context';
import { BookSlot } from './BookSlot'

export const Slot = (props) => {
    const [form, showForm] = React.useState(false);
    const [slot, setSlot] = React.useState({})

    const hideForm = () => showForm(false)

    return (
        <MyContext.Consumer>
            {(value) => {
                return (
                    <div className="card m-3">
                        <div className="card-body">
                            <div className="card-title">
                                <p className="text-right cur" onClick={() => props.hideSlot()}>X</p>
                                <p className="h4 text-center">{props.date + ' Slots'}</p>
                            </div>
                            <hr />
                            <div className="card-text">
                                <div className="row">
                                    {props.slots.map((i, index) => {
                                        return (
                                            <div className="col-lg-4 col-md-4 col-sm-12" key={index}>
                                                <div className="card">
                                                    <div className="card-body">
                                                        <div className="card-title">
                                                            <p className="h5 text-center">{i.slotName}</p>
                                                        </div>
                                                        <hr />
                                                        <div className="card-text">
                                                            <div className="row justify-content-center m-5">
                                                                <div className="col-sm-12 text-center">
                                                                    <button
                                                                        className={(i.status === 'available') ? "btn btn-success" : 'btn btn-danger'}
                                                                        type='button'
                                                                        onClick={() => {
                                                                            let obj = value[0];
                                                                            obj['slot'] = index
                                                                            value[1](obj);

                                                                            if (i.status === 'available') {
                                                                                setSlot(i)
                                                                                showForm(true)
                                                                            } else {
                                                                                alert('slot booked please select another slot')
                                                                            }
                                                                        }}>Book Slot</button>
                                                                </div>
                                                            </div>
                                                            <p className="text-center">{'Time : ' + i.startTime + ' to ' + i.endTime}</p>
                                                            <p className="text-center">{'Status : ' + i.status}</p>
                                                        </div>
                                                    </div>
                                                </div>
                                            </div>
                                        )
                                    })}
                                </div>
                                {form ? <div className="row justify-content-center">
                                    <div className="col-lg-6 col-md-8 col-sm-10">
                                        <BookSlot hideForm={hideForm} slot={slot} />
                                    </div>
                                </div>
                                    : null}
                            </div>
                        </div>
                    </div>
                )
            }}
        </MyContext.Consumer>

    )
} 