import React from 'react'
import { Room } from './Room';

export const Rooms = () => {
    const [rooms, setRooms] = React.useState([]);

    React.useEffect(() => {
        fetch('https://damp-caverns-01669.herokuapp.com/rooms').then((data) => data.json()).then((data) => setRooms(rooms.concat(data)))
    }, [])

    return (
        <div className="container">
            {rooms.map((i, index) => {
                return (
                    <Room room={i} key={index} />
                )
            })}
        </div>
    )
}