import React from 'react'
import { MyContext } from '../Context/Context';
import { RoomData } from './RoomData';
import { Slot } from "./Slot";

export const Room = (props) => {
    const [slots, setSlots] = React.useState([]);
    const [date, setDate] = React.useState('');
    const [data, showData] = React.useState(false);
    const [slot, showSlots] = React.useState(false);
    const getSlots = (slts, date) => {
        showSlots(true)
        setSlots(slts);
        setDate(date);
    }
    const hideSlot = () => {
        showSlots(false);
    }

    return (
        <MyContext.Consumer>
            {(value) => {
                return (
                    <div className="mt-5 mb-5">
                        <div className="card">
                            <div className="card-body">
                                <div className="row">
                                    <div className="col-sm-6">
                                        <div className="card">
                                            <div className="card-body">
                                                <div className="card-title">
                                                    <p className="h4 text-center">
                                                        {"Room " + props.room.room}
                                                    </p>
                                                </div>
                                                <hr />
                                                <div className="card-text">
                                                    <p className="h5 text-center m-4">
                                                        {"Price : Rs." + props.room.price}
                                                    </p>
                                                    <p className="h5 text-center m-4">
                                                        {"Seats " + props.room.seats}
                                                    </p>
                                                </div>
                                            </div>
                                        </div>
                                    </div>
                                    <div className="col-sm-6">
                                        <div className="card">
                                            <div className="card-body">
                                                <div className="card-title">
                                                    <p className="h4 text-center">Dates</p>
                                                </div>
                                                <hr />
                                                <div className="card-text">
                                                    <div className="row">
                                                        <div className="col-sm-12 mt-2 mb-2">
                                                            <div className="row">
                                                                {props.room.dates.map((a, b) => {
                                                                    return (
                                                                        <div className="col-lg-4 col-md-12 col-sm-12 mb-2 text-center" key={b}>
                                                                            <button className={(a.status === 'available') ? "btn btn-success" : 'btn btn-danger'}
                                                                                onClick={() => {
                                                                                    value[1]({
                                                                                        room: props.room,
                                                                                        date: b,
                                                                                        slot: ''
                                                                                    });
                                                                                    console.log(a.status)
                                                                                    if (a.status === 'available') {
                                                                                        getSlots(a.slots, a.date);
                                                                                    } else {
                                                                                        alert('All slots are filled for this date');
                                                                                    }
                                                                                }}
                                                                            >
                                                                                {a.date}
                                                                            </button>
                                                                            <p className="text-center">Status : {a.status}</p>
                                                                        </div>
                                                                    )
                                                                })}
                                                            </div>
                                                        </div>
                                                        <div className="col-sm-12 text-center mt-2 mb-2">
                                                            <p className="h6">Room Status : {props.room.status}</p>
                                                        </div>
                                                    </div>
                                                </div>
                                            </div>
                                        </div>
                                    </div>
                                </div>
                                {slot ? <Slot slots={slots} date={date} hideSlot={hideSlot} /> : null}
                                <div className="row mt-5 justify-content-center">
                                    <div className="col-lg-4 col-md-6 col-sm-6 text-center">
                                        <button className="btn btn-block btn-primary" onClick={() => {
                                            showData(!data)
                                        }}>
                                            {(!data) ? 'Show Booked Customers' : 'hide Data'}
                                        </button>
                                    </div>
                                </div>
                                {(data) ?
                                    <div className="row m-5 tableRow">
                                        <RoomData room={props.room} />
                                    </div> : null}
                            </div>
                        </div>
                    </div>
                )
            }}

        </MyContext.Consumer>
    )
}